"use client";

import { useState, useEffect } from "react";
import { useAuth } from "@/context/AuthContext";
import { Plus, Trash2, Search } from "lucide-react";

interface Stock {
    symbol: string;
    current_price: number;
    rank: number;
}

export default function ManageStocks() {
    const { token } = useAuth();
    const [stocks, setStocks] = useState<Stock[]>([]);
    const [newSymbol, setNewSymbol] = useState("");
    const [isLoading, setIsLoading] = useState(false);

    const fetchStocks = async () => {
        try {
            const res = await fetch("http://localhost:8000/api/v1/watchlist");
            const data = await res.json();
            setStocks(Array.isArray(data) ? data : []);
        } catch (error) {
            console.error("Failed to fetch stocks", error);
        }
    };

    useEffect(() => {
        fetchStocks();
    }, []);

    const handleAddStock = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!newSymbol) return;

        setIsLoading(true);
        try {
            const res = await fetch("http://localhost:8000/api/v1/admin/stocks", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${token}`
                },
                body: JSON.stringify({ symbol: newSymbol.toUpperCase(), action: "add" })
            });

            if (res.ok) {
                setNewSymbol("");
                fetchStocks();
            } else {
                alert("Failed to add stock");
            }
        } catch (error) {
            console.error(error);
            alert("Error adding stock");
        } finally {
            setIsLoading(false);
        }
    };

    const handleDeleteStock = async (symbol: string) => {
        if (!confirm(`Are you sure you want to delete ${symbol}?`)) return;

        try {
            const res = await fetch(`http://localhost:8000/api/v1/admin/stocks/${symbol}`, {
                method: "DELETE",
                headers: {
                    "Authorization": `Bearer ${token}`
                }
            });

            if (res.ok) {
                fetchStocks();
            } else {
                alert("Failed to delete stock");
            }
        } catch (error) {
            console.error(error);
            alert("Error deleting stock");
        }
    };

    return (
        <div className="space-y-8">
            <h2 className="text-3xl font-bold text-white">Manage Stocks</h2>

            {/* Add Stock Form */}
            <div className="bg-gray-800 p-6 rounded-2xl border border-gray-700">
                <h3 className="text-xl font-semibold text-white mb-4">Add New Stock</h3>
                <form onSubmit={handleAddStock} className="flex gap-4">
                    <div className="relative flex-1">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                        <input
                            type="text"
                            placeholder="Enter Stock Symbol (e.g., RELIANCE)"
                            value={newSymbol}
                            onChange={(e) => setNewSymbol(e.target.value)}
                            className="w-full bg-gray-900 border border-gray-700 rounded-xl py-3 pl-10 pr-4 text-white focus:outline-none focus:border-blue-500"
                        />
                    </div>
                    <button
                        type="submit"
                        disabled={isLoading}
                        className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-xl font-medium flex items-center gap-2 transition-colors disabled:opacity-50"
                    >
                        <Plus className="w-5 h-5" />
                        Add Stock
                    </button>
                </form>
            </div>

            {/* Stocks List */}
            <div className="bg-gray-800 rounded-2xl border border-gray-700 overflow-hidden">
                <table className="w-full text-left">
                    <thead className="bg-gray-900/50">
                        <tr>
                            <th className="p-4 text-gray-400 font-medium">Symbol</th>
                            <th className="p-4 text-gray-400 font-medium">Price</th>
                            <th className="p-4 text-gray-400 font-medium">Rank</th>
                            <th className="p-4 text-gray-400 font-medium text-right">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-700">
                        {stocks.map((stock) => (
                            <tr key={stock.symbol} className="hover:bg-gray-700/50 transition-colors">
                                <td className="p-4 font-medium text-white">{stock.symbol}</td>
                                <td className="p-4 text-gray-300">â‚¹{stock.current_price.toFixed(2)}</td>
                                <td className="p-4 text-gray-300">#{stock.rank}</td>
                                <td className="p-4 text-right">
                                    <button
                                        onClick={() => handleDeleteStock(stock.symbol)}
                                        className="p-2 text-red-400 hover:bg-red-900/20 rounded-lg transition-colors"
                                        title="Delete Stock"
                                    >
                                        <Trash2 className="w-5 h-5" />
                                    </button>
                                </td>
                            </tr>
                        ))}
                        {stocks.length === 0 && (
                            <tr>
                                <td colSpan={4} className="p-8 text-center text-gray-500">
                                    No stocks tracked yet. Add one above!
                                </td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    );
}
